<script setup>

  import { onMounted, onUnmounted,reactive,markRaw, ref, toRefs,computed } from 'vue';
  import { useLocaler, useLocale } from 'vue-localer';
  import axios from "axios";
  import VideoPlayer from '@/components/element/VideoPlayer.vue'
  import MysteryBox from '@/components/element/MysteryBox.vue'
  import Hero from '@/components/element/Hero.vue'
  import Loading from '@/components/element/Loading.vue';
  import { Icon } from '@iconify/vue';
    import { useRoute } from 'vue-router'
  import { BigNumber, ethers } from "ethers";
  import { popup,toast,confirm,claimMysteryDialog,verifyMysteryDialog,whitelistMysteryDialog } from '@/libraries/dialog.js'
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  import eventBus from '@/libraries/eventBus.js';

  const language = useLocale();
  const route = useRoute();

  const { account,providerWeb3Modal } = mapState('wallet');
  const { connectWallet } = mapActions('wallet');
  const { setConfirms } = mapMutations('wallet');
  const { isUserConnected,getBalanceEth } = mapGetters('wallet');

  const { setCount,setMyBox } = mapMutations('mystery');
  const { host,count,buying,claiming,opening,myBoxListing,myBoxes } = mapState('mystery');
  const { buyMysteryBox,claimMysteryBox,openMysteryBox,getMyBoxes,getMyBox } = mapActions('mystery');

  const { myHeroes, myHeroIds, myHeroListing } = mapState('hero');
  const { getMyHeroes } = mapActions('hero');
  const { setMyHero } = mapMutations('hero');

  const { apiHost,heroHost,analysis } = mapState('game');

  eventBus.on("onMysteryOpened",async function(e){
    setConfirms(-1);
    console.log(e.id);
    mystery.processer[e.id]=false;
    setMyBox(e);
    if(e.box.opened==1)
    {
      if(e.box.airdrop>0){
        popup("success",language.value.mystery.dialog.open.success.replace("{num}",e.box.airdrop));        
      }
    }else{
      popup("error",language.value.mystery.dialog.open.timeout);
    }
  });
  

  const mystery = reactive({
    count:1,
    timer:null,
    processer:{},
    verifing:false,
    startTime:{
      days:0,
      hours:0,
      minutes:0,
      seconds:0
    },
    goToTop : ()=>{
      window.scrollTo(0,0);
    },
    buy: async () =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      let amount = (count.value * analysis.value.normalPrice).toFixed(2)+0;
      let balance = getBalanceEth.value+0;
      if(balance>=amount)
      {
        await buyMysteryBox([]);
      }else{
        popup('error',language.value.wallet.insufficientBalance);
      }
    },
    claim: async () =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      let data = await claimMysteryDialog(language.value);
      if(!data){
        return;
      }

      if(data.count<=0){
        popup('error',language.value.mystery.buying.insufficientCount);
        return;
      }

      if(data.signature.length<=0){
        popup('error',language.value.mystery.buying.insufficientSignature);
        return;
      }

      await claimMysteryBox({count:data.count,signature:data.signature});
    },
    whitelist: async ()=>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      let data = await whitelistMysteryDialog(language.value);
      if(!data){
        return;
      }

      if(data.count<=0){
        popup('error',language.value.mystery.buying.insufficientCount);
        return;
      }

      if(data.signature.length<=0){
        popup('error',language.value.mystery.buying.insufficientSignature);
        return;
      }

      setCount(data.count);

      await buyMysteryBox({signature:data.signature});
      
    },
    open: async (id) =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 
            mystery.processer[id]=true;
      await openMysteryBox({id});
      mystery.processer[id]=false;
    },
    verify: async (blockNumber,boxId) =>{
      if (!isUserConnected.value) {
        await connectWallet();
        return;
      } 

      try{
        mystery.verifing=true;

        let provider = new ethers.providers.Web3Provider(providerWeb3Modal.value);
        let hashcode = await provider.getBlock(blockNumber);

        mystery.verifing=false;
        if(hashcode.hash.length!=66){
          popup('error',"Can not to get block hash");
          return;
        }

        await verifyMysteryDialog(account.value,blockNumber,hashcode.hash,boxId,language.value);

      }catch(e){
        popup('error',e.message);
      }

    },
    max: () =>{
      mystery.count = 10;
      setCount(mystery.count);
    },
    double: () =>{
      let m = mystery.count * 2;
      mystery.count = m > 10 ? 10 : m;
      setCount(mystery.count);
    },
    half: () =>{
      let m = Math.round(mystery.count / 2.0);
      mystery.count = m < 0 ? 1 : m;
      setCount(mystery.count);
    },
    change: (value) =>{

      let m = parseInt(value);
      if(m<=0)
      {
        m=1;
      }
      if(m>20)
      {
        m=20;
      }
      if(!value){
        m=0;
      }
      mystery.count=m;
      setCount(mystery.count);
    },
    tab:{
      index:0,
      switch: async (index) =>{
        mystery.tab.index = index;

        if (!isUserConnected.value && index>0) {
          await connectWallet();
          return;
        }
        
        if(index==1){
          await getMyBoxes();
        }else if(index==2){
          await getMyHeroes();
        }
      }
    },
    getRarityName: (rarity)=>{
        if(rarity == 1){
            return language.value.mystery.table.rarity.standard;
        }else if(rarity == 2){
            return language.value.mystery.table.rarity.execllent;
        }else if(rarity == 3){
            return language.value.mystery.table.rarity.rary;
        }else if(rarity == 4){
            return language.value.mystery.table.rarity.epic;
        }else if(rarity == 5){
            return language.value.mystery.table.rarity.miracle;
        }else if(rarity == 6){
            return language.value.mystery.table.rarity.legendary;
        }
    },
    getRarityColor: (rarity)=>{
        if(rarity == 1){
            return "bg-gray-700";
        }else if(rarity == 2){
            return "bg-green-700";
        }else if(rarity == 3){
            return "bg-blue-700";
        }else if(rarity == 4){
            return "bg-purple-700";
        }else if(rarity == 5){
            return "bg-orange-700";
        }else if(rarity == 6){
            return "bg-red-700";
        }
    },
    getRoleName: (role) =>{
        if(role == 1){
            return language.value.mystery.role.warrior;
        }else if(role == 2){
            return language.value.mystery.role.mage;
        }else if(role == 3){
            return language.value.mystery.role.hunter;
        }else if(role == 4){
            return language.value.mystery.role.sniper;
        }else if(role == 5){
            return language.value.mystery.role.boxer;
        }else if(role == 6){
            return language.value.mystery.role.monk;
        }
    },
    onTimer : ()=>{

      if(mystery.tab.index==2){
        let url = apiHost.value+"/hero/queue";
        axios.post(url,myHeroIds.value)
        .then(r => {
          if(r.data && r.data.data){
            let items = r.data.data;
            for(let i=0;i<items.length;i++)
            {
              setMyHero(items[i]);
            }
          }
        })
        .catch(e => {
          console.log(e);
        })
        .finally(() => {
        });
      }

      var nowTime = Math.floor(Date.now() / 1000);
      if (analysis.value.startTime >= nowTime) {
        var lefttime = analysis.value.startTime - nowTime;
        var timespan = lefttime % 86400;
        var days = (lefttime - timespan) / 86400;
        var remain = timespan % 3600;
        var hours = (timespan - remain) / 3600;
        var minutes = (remain - remain % 60) / 60;
        var seconds = timespan % 60;
        if (minutes >= 0 && seconds >= 0) {
          mystery.startTime.days = days;
          mystery.startTime.hours = hours;
          mystery.startTime.minutes = minutes;
          mystery.startTime.seconds = seconds;
        }
      }

    },
    base64Encode: (data) =>{
      return window.encodeURI(window.btoa(data));
    },
    base64Decode: (data) =>{
      return window.decodeURI(window.atob(data));
    },
    isInteger: (value) => {
        var x;
        if (isNaN(value)) {
          return false;
        }
        x = parseFloat(value);
        return (x | 0) === x;
    },
    start: () =>{
      mystery.timer = setInterval(mystery.onTimer, 1000);
      
      let rererrerId= route.params.id;
      if(rererrerId && rererrerId.length>0){
        let id = mystery.base64Decode(rererrerId);
        localStorage.setItem("Hashland_Referrer_Id",mystery.isInteger(id)?id:0);
      }
      
    },
    stop: () =>{
      clearInterval(mystery.timer);
    },
    currency: (value)=>{
      return (+value || 0).toFixed(0).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,')
    }
  });

  onMounted(() => {
    mystery.start();
  });

  onUnmounted(()=>{
    mystery.stop();
  });

</script>

