<style>
.heartbeat{-webkit-animation:heartbeat 1.5s ease-in-out infinite both;animation:heartbeat 1.5s ease-in-out infinite both}

.mystery{
  background: url('/images/mystery.jpg') no-repeat center;
  background-size: cover;
}

.mask{
    z-index: 1;
    left: 0;
    bottom: -2px;
    position: absolute;
    pointer-events: none;
    width: 100%;
    display: block;
    overflow: hidden;
    pointer-events: none;
}

.overlay {
  background-image: linear-gradient(0deg, #0d022c00 50%, #1b0c57ba 100%);
}

.signature {
    box-sizing: border-box;
    width: auto;
    transition: border-color .1s,box-shadow .1s;
    border: 1px solid #d9d9d9;
    border-radius: 0.1875em;
    background: inherit;
    box-shadow: inset 0 1px 1px rgb(0 0 0 / 6%), 0 0 0 3px transparent;
    color: inherit;
    font-size: 1.125em;
    height: 6.75em;
    padding: 0.75em;
}

.vertical-center {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.tab-active{
  @apply text-primary border-primary ;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
      -webkit-appearance: none; 
      margin: 0; 
}
input[type=number] { 
    -moz-appearance: textfield;
    appearance: textfield;
    margin: 0; 
}

.flex-parent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.input-flex-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 80vw;
  height: 100px;
  position: relative;
  z-index: 0;
}

.input {
  width: 25px;
  height: 25px;
  background-color: #DA3174;
  position: relative;
  border-radius: 50%;
}
.input:hover {
  cursor: pointer;
}
.input::before, .input::after {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  top: 50%;
  transform: translateY(-50%);
  background-color: #DA3174;
  width: 8vw;
  height: 5px;
  max-width: 100px;
}
.input::before {
  left: calc(-8vw + 12.5px);
}
.input::after {
  right: calc(-8vw + 12.5px);
}
.input.active {
  background-color: #DA3174;
}
.input.active::before {
  background-color: #DA3174;
}
.input.active::after {
  background-color: #2a263e;
}
.input.active span {
  color: #DA3174;
}
.input.active span::before {
  font-size: 13px;
}
.input.active span::after {
  font-size: 15px;
}
.input.active ~ .input, .input.active ~ .input::before, .input.active ~ .input::after {
  background-color: #2a263e;
}
.input span {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  visibility: hidden;
}
.input span::before, .input span::after {
  visibility: visible;
  position: absolute;
  left: 50%;
}
.input span::after {
  content: attr(data-year);
  top: 25px;
  transform: translateX(-50%);
  font-size: 14px;
}
.input span::before {
  content: attr(data-info);
  top: -65px;
  width: 70px;
  transform: translateX(-5px) rotateZ(-45deg);
  font-size: 12px;
  text-indent: -10px;
}

.description-flex-container {
  width: 80vw;
  font-weight: 400;
  font-size: 22px;
  max-width: 1000px;
}
.description-flex-container p {
  margin-top: 0;
  display: none;
}
.description-flex-container p.active {
  display: block;
}

@media (min-width: 1250px) {
  .input::before {
    left: -87.5px;
  }

  .input::after {
    right: -87.5px;
  }
}
@media (max-width: 850px) {
  .input {
    width: 17px;
    height: 17px;
  }
  .input::before, .input::after {
    height: 3px;
  }
  .input::before {
    left: calc(-8vw + 8.5px);
  }
  .input::after {
    right: calc(-8vw + 8.5px);
